.progress {
    width: 86px;
    height: 8px;
    border-radius: 4px;
    appearance: none;
}
.progress::-webkit-progress-bar {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 4px;
}
.progress::-webkit-progress-value {
    background-color: white;
    border-radius: 4px;
}
