.progress {
    width: 100%;
    height: 10px;
    border-radius: 5px;
    appearance: none;
}
.progress::-webkit-progress-bar {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 5px;
}
.progress::-webkit-progress-value {
    background-color: red;
    border-radius: 5px;
}
